.laptop {
  position: relative;
  margin: auto;
  max-width: 80rem;
}

.laptop .laptop__screen {
  position: relative;
  z-index: 1;
  padding: 4%;
  border-radius: 2rem;
  background: #ecf1f7;
  background-image: linear-gradient(to bottom, #333, #111);
  box-shadow: 0 0.1rem 0 #cfcfcf;
  border: 2px solid #ccc;
  border-bottom: none;
}

.laptop .laptop__screen img {
  display: block;
  max-width: 100%;
  height: auto;
  aspect-ratio: attr(width)/attr(height);
  background: #000;
}

.laptop .laptop__bottom {
  position: relative;
  z-index: 1;
  margin-right: -7%;
  margin-left: -7%;
  height: 0.7rem;
  background: #e9eff5;
  background-image: linear-gradient(to right, #d2dde9 0%, #f9fcff 15%, #e5ebf2 40%, #e5ebf2 60%, #f9fcff 85%, #d2dde9 100%);
}

.laptop .laptop__bottom::before {
  display: block;
  margin: 0 auto;
  width: 20%;
  height: 0.7rem;
  border-radius: 0 0 0.2rem 0.2rem;
  background: #f6f9fc;
  background-image: linear-gradient(to right, #c3cfdb 0%, #f6f9fc 10%, #f6f9fc 90%, #c3cfdb 100%);
  content: " ";
}

.laptop .laptop__under {
  position: absolute;
  top: 100%;
  left: 25%;
  display: block;
  width: 50%;
  height: 0.7rem;
  background: #e2e8f0;
  background-image: linear-gradient(to bottom, #e2e8f0, #bec7d1);
}

.laptop .laptop__under::after, .laptop .laptop__under::before {
  position: absolute;
  top: 0%;
  right: 100%;
  bottom: 0;
  display: block;
  width: 50%;
  border-bottom-left-radius: 100%;
  background: inherit;
  content: " ";
}

.laptop .laptop__under::after {
  right: auto;
  left: 100%;
  border-bottom-right-radius: 100%;
  border-bottom-left-radius: 0;
}

.laptop .laptop__shadow {
  position: absolute;
  right: -10%;
  bottom: -1.5rem;
  left: -10%;
  z-index: 0;
  height: 1rem;
  background: radial-gradient(ellipse closest-side, #000, transparent);
  opacity: 0.5;
}

